import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import axios from "axios";

/* img import */
import facebook from "../img/icon_facebook.png";
import twitter from "../img/icon_twitter.png";
import blog from "../img/icon_blog.png";
import instagram from "../img/icon_instgram.png";
import youtube from "../img/icon_youtube.png";
import logo from "../img/logo_baskinrobbins.png";
import openSearch from "../img//icon_search.png";
import closeSearch from "../img/btn_search_close.gif";

const Head = styled.header`
  border-top: 3px solid #ff7c98;

  /*header       */
  .logo_menu {
    margin: 0 auto;
    .logo {
      height: 136px;
      border-bottom: 1px solid #e2d9d6;
      background: url(../img/bg_header.gif);
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      /*      .*/
      flex-shrink: 1;
      ul {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0;

        &:nth-child(2) {
          position: relative;
          top: 5px;
        }

        li {
          cursor: pointer;
          margin-right: 10px;
        }
      }

      .left_logo,
      .search {
        position: relative;
        top: 32px;
      }

      .search {
        li {
          font-size: 11px;
          padding-top: 10px;
          padding-left: 10px;
          &:last-child {
            position: relative;
            top: -20px;
          }
        }
      }
    }
  }
`;

/*     . */
const Menu = styled.div`
  border-bottom: 1px solid #3f291a;
  height: 45px;
  .menu {
    width: 1200px;
    margin: 0 auto;
    height: 100%;
    display: flex;
    position: relative;
    left: -20px;
    ul {
      flex-grow: 1;
      height: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      margin: 0;
      padding-left: 0;

      li {
        span {
          display: block;
          height: 100%;
          background: url(../img/gnb_menu.png);
          text-indent: -99999em;
        }
        &:nth-child(1) {
          span {
            width: 36px;
            background-position: 0px -1px;
          }
        }
        &:nth-child(2) {
          span {
            width: 36px;
            background-position: -93px -1px;
          }
        }
        &:nth-child(3) {
          span {
            position: relative;
            top: -4px;
            width: 167px;
            background-position: -130px -1px;
          }
        }
        &:nth-child(4) {
          span {
            position: relative;
            top: -4px;
            width: 42px;
            background-position: -415px -1px;
          }
        }
        &:nth-child(5) {
          span {
            position: relative;
            top: -4px;
            width: 105px;
            background-position: -545px -1px;
          }
        }
        &:nth-child(6) {
          span {
            position: relative;
            top: -4px;
            width: 44px;
            background-position: 370px -1px;
          }
        }
        &:nth-child(7) {
          span {
            position: relative;
            top: -4px;
            width: 50px;
            background-position: 210px -1px;
          }
        }
        &:nth-child(8) {
          span {
            position: relative;
            top: -4px;
            width: 49px;
            background-position: 49px -1px;
          }
        }
      }
    }
  }
`;

const HiddenMenu = styled.div`
  /* showMenu, hideMenu    css   */
  // max-height 0  padding  height  .
  // overflow: hidden, box-sizing: border-box   .
  //     css .
  // pading    padding  .
  .showMenu {
    max-height: 275px;
    border-bottom: 1px solid #3f291a;
    box-sizing: border-box;
    overflow: hidden;
    transition: max-height 0.5s ease-out;
    div {
      width: 1200px;
      margin: auto;
      ul {
        display: flex;
        justify-content: space-evenly;
        position: relative;
        margin: 0;
        padding: 0;
        .pic1 {
          position: relative;
          top: 60px;
        }

        li {
          ul {
            display: flex;
            flex-direction: column;
            padding: 0;
            li {
              text-align: center;
              font-size: 14px;
              color: #948780;
              box-sizing: border-box;
              &:hover {
                color: #ff7c98;
                cursor: pointer;
              }
            }
          }
          &:nth-child(3),
          &:nth-child(4),
          &:nth-child(5),
          &:nth-child(6),
          &:nth-child(7) {
            ul {
              position: relative;
              top: 30px;
            }
          }
          &:nth-child(3) {
            li {
              padding: 10px 20px 10px 40px;
            }
          }
          &:nth-child(4) {
            li {
              padding: 10px 30px;
            }
          }
          &:nth-child(5) {
            li {
              padding: 10px 25px 10px 35px;
            }
          }
          &:nth-child(6) {
            li {
              padding: 10px 40px 10px 10px;
            }
          }
          &:nth-child(7) {
            li {
              padding: 10px 60px 10px 0px;
            }
          }
        }
      }
    }
  }
  .hideMenu {
    max-height: 0;
    box-sizing: border-box;
    overflow: hidden;
    transition: max-height 0.5s ease-out;
    div {
      width: 1200px;
      margin: auto;
      ul {
        display: flex;
        justify-content: space-evenly;
        position: relative;
        margin: 0;
        padding: 0;
        .pic1 {
          position: relative;
          top: 60px;
        }

        li {
          ul {
            display: flex;
            flex-direction: column;
            padding: 0;
            li {
              text-align: center;
              font-size: 14px;
              color: #948780;
              box-sizing: border-box;
              &:hover {
                color: #ff7c98;
                cursor: pointer;
              }
            }
          }
          &:nth-child(3),
          &:nth-child(4),
          &:nth-child(5),
          &:nth-child(6),
          &:nth-child(7) {
            ul {
              position: relative;
              top: 30px;
            }
          }
          &:nth-child(3) {
            li {
              padding: 10px 20px 10px 40px;
            }
          }
          &:nth-child(4) {
            li {
              padding: 10px 30px;
            }
          }
          &:nth-child(5) {
            li {
              padding: 10px 25px 10px 35px;
            }
          }
          &:nth-child(6) {
            li {
              padding: 10px 40px 10px 10px;
            }
          }
          &:nth-child(7) {
            li {
              padding: 10px 60px 10px 0px;
            }
          }
        }
      }
    }
  }
`;

const SearchMenu = styled.div`
  width: 1200px;
  overflow: hidden;
  margin: auto;
  box-sizing: border-box;
  table {
    font-size: 13px;
    padding-top: 25px;
    tbody {
      tr {
        vertical-align: top;
        th {
          text-align: left;
          font-size: 14px;
          color: #2f231c;
          font-weight: 100;
          padding-top: 8px;
          width: 100px;
        }
        &:nth-child(1) {
          td {
            padding-right: 20px;
            div {
              display: block;
              float: left;
              select {
                background: #fff;

                border: 1px solid #d1cecc;
                border-radius: 4px;
                width: 128px;
                color: #636363;
                margin-right: 14px;
                padding: 8px;
                &:focus {
                  outline: none;
                }
              }
              input {
                height: 32px;
                padding: 8px 1px 8px 10px;
                border: 0;
                background: #efefef;
                line-height: 16px;
                box-sizing: border-box;
                &:focus {
                  outline: none;
                }
              }
            }
          }
          .width1 {
            width: 500px;
            div {
              input {
                width: 270px;
              }
            }
          }
          .width2 {
            div {
              width: 482px;
              input {
                width: 540px;
                color: #ff7c98;
              }
            }
            div {
              width: 542px;
              p {
                color: #9c9c9c;
              }
              a.active {
                color: #ff7c98;
                margin-right: 6px;
                display: inline-block;
              }
            }
          }
        }

        &:nth-child(2) {
          th.al {
            width: 150px;
          }
          td.width3 {
            div {
              width: 300px;
              label {
                padding-bottom: 10px;
                width: 100px;
                input[type="checkbox"] {
                  display: none;
                }
                input[type="checkbox"] + span {
                  margin: 0;
                  padding-bottom: 10px;
                  width: 72px;
                  cursor: pointer;
                  display: block;
                  float: left;
                  line-height: 17px;
                  padding-left: 23px;
                  background: url(../img/bg_check_off.gif) no-repeat;
                }
                input[type="checkbox"]:checked + span {
                  background: url(../img/bg_check_on.gif);
                }
              }
            }
          }
        }
      }
    }
  }
  div.button {
    display: flex;
    padding-bottom: 25px;
    button {
      margin: auto;
      width: 147px;
      height: 37px;
      background: #ff7c98;
      color: #fff;
      font-size: 15px;
      line-height: 37px;
      border-radius: 18px;
      border: 0;
      cursor: pointer;
    }
  }
`;

const Pikachu = styled.div`
  height: 150px;
  background: url(../img/1714824579.jpg) center no-repeat;
  background-color: #fdd827;
  position: relative;
`;

const Header = () => {
  const [Data, setData] = React.useState([]);
  const [menu, setMenu] = React.useState(false);
  const [search, setSearch] = React.useState(false);

  const Hover = () => {
    search === true
      ? setMenu(false)
      : menu === false
      ? setMenu(true)
      : setMenu(false);
  };
  const remove = () => {
    menu === true ? setMenu(false) : setMenu(true);
  };
  const onClickSearch = () => {
    search === false ? setSearch(true) : setSearch(false);
  };

  React.useEffect(() => {
    (async () => {
      let json = null;
      try {
        const response = await axios.get("http://localhost:3008/header");
        json = response.data;
      } catch (e) {
        console.error(e);
        alert("Ajax  ");
      }
      if (json != null) {
        //      
        setData((Data) => json);
      }
    })();
  }, []);
  console.log(Data);
  return (
    <div>
      <Head>
        <div className="logo_menu">
          <div className="logo">
            <ul className="left_logo">
              <li>
                <img src={facebook} alt={facebook} />
              </li>
              <li>
                <img src={twitter} alt={twitter} />
              </li>
              <li>
                <img src={blog} alt={blog} />
              </li>
              <li>
                <img src={instagram} alt={instagram} />
              </li>
              <li>
                <img src={youtube} alt={youtube} />
              </li>
            </ul>
            <ul>
              <li>
                <img src={logo} alt={logo} />
              </li>
            </ul>
            <ul className="search">
              <li>
                <NavLink to="#"></NavLink>
              </li>
              <li>
                <NavLink to="#">CONTACT US</NavLink>
              </li>
              {/*    */}
              <li onClick={onClickSearch}>
                <img
                  //  on off  button  .
                  src={search === true ? closeSearch : openSearch}
                  alt={search}
                />
              </li>
            </ul>
          </div>
          <Menu>
            <div className="menu">
              {/*        mouseover      */}
              {/*         html      ..--css  */}
              <ul onMouseEnter={Hover}>
                <li className="first">
                  <NavLink to="#">
                    <span>LOGIN</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#">
                    <span>JOIN</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#">
                    <span>FLAVOR OF THE MONTH</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#">
                    <span>MENU</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#">
                    <span>   </span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#">
                    <span>EVENT</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#" className="link">
                    <span>STORE</span>
                  </NavLink>
                </li>
                <li>
                  <NavLink to="#" className="link">
                    <span>ABOUT</span>
                  </NavLink>
                </li>
              </ul>
            </div>
          </Menu>
        </div>
      </Head>
      <HiddenMenu>
        {/*   -->       css   --> css  */}
        <div
          className={menu === true ? "showMenu" : "hideMenu"}
          onMouseLeave={remove}
        >
          <div>
            <ul>
              <li className="pic1">
                <img src="../img/img_happypoint_app.jpg" alt="pic" />
              </li>
              <li>
                <NavLink to="#">
                  <img src="../img/img_monthly_fom_220429.png" alt="img" />
                </NavLink>
              </li>
              <li>
                <ul>
                  <li></li>
                  <li> </li>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </li>
              <li>
                <ul>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </li>
              <li>
                <ul>
                  <li></li>
                  <li></li>
                </ul>
              </li>
              <li>
                <ul>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </li>
              <li>
                <ul>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li>CONTACT US</li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </HiddenMenu>
      {/* css            CSS    --> css   */}
      <SearchMenu
        style={{
          maxHeight: search === true ? "313px" : "0px",
          transition: search === true ? "max-height 0.5s ease-out" : " ",
        }}
      >
        <div>
          <table>
            <tbody>
              <tr>
                <th></th>
                <td className="width1">
                  <div>
                    <select>
                      <option></option>
                      <option></option>
                      <option> </option>
                      <option></option>
                      <option></option>
                      <option></option>
                      <option>block pack</option>
                      <option>ready pack</option>
                    </select>
                  </div>
                  <div>
                    <input type="text" />
                  </div>
                </td>
                <th></th>
                <td className="width2">
                  <div>
                    <input type="text" />
                  </div>
                  <div>
                    <p> &#183;   </p>
                    <nav>
                      <NavLink to="#">#</NavLink>
                      <NavLink to="#">#</NavLink>
                      <NavLink to="#">#</NavLink>
                      <NavLink to="#">#</NavLink>
                      <NavLink to="#">#</NavLink>
                      <NavLink to="#">#</NavLink>
                    </nav>
                  </div>
                </td>
              </tr>
              <tr>
                <th className="al"> </th>
                <td className="width3" colSpan={3}>
                  <div>
                    {Data &&
                      Data.map((v, i) => {
                        return (
                          <label key={i}>
                            <input type="checkbox" name="checker" />
                            <span>{v.checkBox}</span>
                          </label>
                        );
                      })}
                  </div>
                  <br />
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="button">
          <button type="submit"></button>
        </div>
      </SearchMenu>
      <Pikachu />
    </div>
  );
};

export default Header;
