import React, { useState } from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

const Div = styled.div`
  padding-bottom: 60px;
  border-top: 1px solid #feaf2b;
  .first_ul {
    padding: 0 80px;
    width: 1200px;
    display: flex;
    margin: auto;
    justify-content: center;
    height: 77px;
    li {
      font-size: 14px;
      position: relative;
      .active {
        top: 50%;
      }
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        width: 12%;
        .active {
          position: absolute;
          color: #726454;
        }
      }
      &:nth-child(5) {
        width: 15%;
        .active {
          position: absolute;
          color: #ff7c98;
          font-weight: bold;
        }
      }
      &:nth-child(6) {
        width: 20%;
        .active {
          position: absolute;
          color: #726454;
        }
      }
      &:nth-child(7) {
        .active {
          position: relative;
          color: #726454;
        }
      }
    }
  }

  .second_ul {
    height: 80px;
    background-color: #f9f8f7;
    div {
      position: relative;
      width: 1200px;
      margin: auto;
      ul {
        padding-top: 22px;
        margin-right: 200px;
        margin-top: 0;
        margin-bottom: 0;
        display: flex;
        position: relative;
        justify-content: center;
        li {
          &:nth-child(1),
          &:nth-child(2),
          &:nth-child(3) {
            padding-right: 50px;
          }
          &:nth-child(4) {
            padding-right: 8px;
          }
          &:nth-child(5) {
            padding-right: 8px;
            img {
              width: 31px;
            }
          }
        }
      }
      .hidden_div {
        display: block;
        float: left;
        position: absolute;
        left: 80%;
        top: 28%;

        button,
        .hidden {
          border: 1px solid #d1cecc;
          background-color: #fff;
          border-radius: 5px;
          padding-left: 11px;
          width: 150px;
          text-align: left;
        }
        button {
          height: 32px;
          font-size: 11px;
          text-align: left;
          color: #b1ab9f;
        }
        /*         */
        .underArrow {
          background: url(./img/family_size_off.png) no-repeat right;
        }
        .upArrow {
          background: url(./img/family_size_on.png) no-repeat right;
        }
        .hidden {
          display: flex;
          flex-direction: column;
          font-size: 13px;
          padding: 5px 0;
          color: #726454;
          position: relative;
          bottom: 266px;
          li {
            padding: 8px 0;
            line-height: 12px;
            text-indent: 10px;
            vertical-align: bottom;
            &:hover {
              background: #efefef;
            }
          }
        }
        .none {
          display: none;
        }
        &:after {
          clear: both;
          float: none;
        }
      }
    }
  }
  .last {
    padding-top: 48px;
    div {
      width: 1200px;
      margin: auto;
      text-align: center;
      div {
        &:nth-child(1) {
          padding-bottom: 30px;
        }
        &:nth-child(2),
        &:nth-child(3) {
          height: 20px;
          display: flex;
          justify-content: center;
          p {
            padding-right: 15px;
            color: #635b56;
            font-size: 11px;
          }
        }
      }
    }
    address {
      text-align: center;
      padding-top: 20px;
      color: #b1adab;
      font-size: 11px;
    }
  }
`;
const Footer = () => {
  const [btn, setBtn] = useState(-1);

  const onClickButton = (e) => {
    btn === 1 ? setBtn(-1) : setBtn(1);
  };
  return (
    <Div>
      <div>
        <ul className="first_ul">
          <li>
            <NavLink to="#"></NavLink>
          </li>
          <li>
            <NavLink to="#"></NavLink>
          </li>
          <li>
            <NavLink to="#"></NavLink>
          </li>
          <li>
            <NavLink to="#"></NavLink>
          </li>
          <li>
            <NavLink to="#"></NavLink>
          </li>
          <li>
            <NavLink to="#"></NavLink>
          </li>
          <li>
            <NavLink to="#"> </NavLink>
          </li>
        </ul>
        <div className="second_ul">
          <div>
            <ul>
              <li>
                <NavLink to="#">
                  <img src="../img/btn_happypoint.png" alt="img" />
                </NavLink>
              </li>
              <li>
                <NavLink to="#">
                  <img src="../img/btn_happymarket.png" alt="img" />
                </NavLink>
              </li>
              <li>
                <NavLink to="#">
                  <img src="../img/btn_spc_story.png" alt="img" />
                </NavLink>
              </li>
              <li>
                <NavLink to="#">
                  <img src="../img/btn_norton.gif" alt="img" />
                </NavLink>
              </li>
              <li>
                <NavLink to="#">
                  <img src="../img/btn_ccm_2.png" alt="img" />
                </NavLink>
              </li>
              <li>
                <NavLink to="#">
                  <img src="../img/btn_ksa.png" alt="img" />
                </NavLink>
              </li>
            </ul>
            <div className="hidden_div">
              {/*   ,       */}
              <button
                type="button"
                onClick={onClickButton}
                className={btn < 0 ? "underArrow" : "upArrow"}
              >
                FAMILY SITE
              </button>
              <ul className={btn < 0 ? "none" : "hidden"}>
                <li> </li>
                <li>SPC</li>
                <li>BR</li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          </div>
        </div>
        <div className="last">
          <div>
            <div>
              <img src="./img/footer_logo.png" alt="footerLogo" />
            </div>
            <div>
              <p>  : 303-81-09535</p>
              <p>()  </p>
              <p>   2620( 11-149)</p>
            </div>
            <div>
              <p>TEL : 080-555-3131</p>
              <p> : </p>
            </div>
            <address>
              Copyright &copy; 2016 BRKOREA Company. All Rights Reserved.
            </address>
          </div>
        </div>
      </div>
    </Div>
  );
};

export default Footer;
